.App{
  background-color: #696969;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
  font-size: calc(10px + 2vmin);
  color: white;
}
.title{
  text-align: center;
  background-color: #002631;
  text-decoration: underline;
  color: rgb(255, 255, 255);
  font-size: 35px;
  margin: 0;
  padding: 10px 0;
}
.footer{
  padding: 10px 0;
  text-align: center;
  margin-top: 30px;
  background-color: #002631;
  color: white;
}



